import React from "react";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import Landing from "../components/Landing";
import { WHY_CLOUDARE, DEV, DEVELOPERS_TESTIMONIALS } from "../constant";
import Partners from "../components/Partners";
import dot from "../assests/svg/dot.svg";
import applyjob from "../assests/images/applyjob.webp";
import Testimonial from "../components/Testimonial";

export default function Apply() {
  return (
    <React.Fragment>
      <div className="layout">
        <Navbar />
        <Landing
          title="Get Jobs with Top Global Companies"
          content="Connecting your talent with the opportunities that youre actually interested in."
          href="https://recruit.cloudare.in/jobs/Careers"
          label="Apply Now"
          src={applyjob}
        />
        <Partners
          list={DEV}
          title="Access jobs with the worlds leading entrprises"
        />
        <section className="box">
          <h1
            style={{
              textAlign: "center",
              padding: "2% 0",
              fontWeight: "400",
              fontFamily: "Ember",
            }}
          >
            Why choose to work with Cloudare
          </h1>
          <div
            className="why-us"
            style={{ borderTop: "2px solid rgb(204, 204, 204)" }}
          >
            {WHY_CLOUDARE.map((data, i) => {
              return (
                <div
                  key={i}
                  className="why-box"
                  style={{ borderBottom: "2px solid #ccc" }}
                >
                  <img className="logo" src={dot} width="30px" height="30px" />
                  <div className="why-content">
                    <h3>{data.title}</h3>
                    <p style={{ margin: "10px 0" }}>{data.desc}</p>
                  </div>
                </div>
              );
            })}
          </div>
        </section>
      
        <Testimonial
          title="Why our Developers Love Us"
          section={DEVELOPERS_TESTIMONIALS}
          id={2}
        />{" "}
      </div>
      <Footer />
    </React.Fragment>
  );
}
