import React from "react";
import logo from "../assests/images/cloudare.webp";
import "../styles/navbar.css";
import { NavLink } from "react-router-dom";

export default function Navigation() {
  return (
    <header className="nav top-nav">
      <div class="cloudare-logo">
        <a href="/">
          <img src={logo} height="90px" alt="logo" />
        </a>
      </div>
      <input id="menu-toggle" type="checkbox" />
      <label className="menu-button-container" for="menu-toggle">
        <div className="menu-button"></div>
      </label>
      <ul className="menu" id="navbar">
        <li className="item">
          {" "}
          <NavLink className="link" exact to="/hire-talent" activeClass="active">
            Hire Talents
          </NavLink>
        </li>
        {/* <li className="item ">
          <NavLink className="link" to="/consultation" activeClass="active">
            Consultation
          </NavLink>
        </li> */}
        <li className="item ">
          <NavLink className="link" to="/payroll" activeClass="active">
            HR Services
          </NavLink>
        </li>
        <li className="item ">
          <NavLink className="link" to="/solutions" activeClass="active">
            Solutions
          </NavLink>
        </li>
        <li className="item">
          {" "}
          <NavLink className="link" to="/apply" activeClass="active">
            Apply for Jobs
          </NavLink>
        </li>
        <li className="item">
          {" "}
          {/* <NavLink to="https://blogs.cloudare.in/" activeClass="active">
          Blogs
          </NavLink> */}
          <a href= "https://blogs.cloudare.in/">Blogs</a>
        </li>
        {/* <li><a className='btn-link' href="/hire">
                    <Button label="Hire Developers"/>
                   </a></li> */}
      </ul>
    </header>
  );
}
